.tapBar{
    position: fixed;
    margin-left: 5%;
    margin-right: 5%;
    bottom: 2%;
    width: 90%;
    text-align: center;
    height: 8%;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-around;
    font-size: 1.5rem;
    align-items: center;
    border-radius: 22px;
    background: linear-gradient(225deg, #21266B, #3a3f88);
    box-shadow: 0px 0px 7px #0c0b0b;
}

.tapBarLight{
    position: fixed;
    margin-left: 5%;
    margin-right: 5%;
    bottom: 2%;
    width: 90%;
    text-align: center;
    height: 8%;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-around;
    font-size: 1.5rem;
    align-items: center;
    border-radius: 22px;
    background: linear-gradient(225deg, rgb(120, 120, 255), #7056f1);
    box-shadow:  -10px 10px 20px #c5c5c5,
                 10px -10px 20px #fbfbfb;
}
.darktheme {
    background-color: rgb(70, 68, 68);
    color: #fff;
}

.lightTheme{
    background-color: #eaebf8;
    color: rgb(45, 46, 44);
}
.button{
    color: white;
}

.buttonLight{
    color: #21266B;
}

.Light{
    color: rgb(255, 153, 0);
}
.Dark{
    color: rgb(200, 184, 68);
}

.buttonMenuLight{
    padding: 1%;
    height: 40px;
    width: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 22px;
    background: linear-gradient(225deg, rgb(120, 120, 255), rgb(188, 188, 248));

}

.buttonMenu{
    padding: 1%;
    height: 40px;
    width: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 22px;
    background: linear-gradient(225deg, #2d338a, #4950a8);
    box-shadow: 0px 0px 7px #0c0b0b;
}

.MenuOpenTabBar{
    width: 90%;
    margin: 55% auto auto auto;
    z-index: 2;
    background-color: rgb(49, 116, 240);
    display: flex;
    justify-content: center;
    align-items: center;
    height: 50px;
    border-radius: 22px;
    position: fixed;
    bottom: 12%;
    left: 5%;
    right: 45%;

}

.triangle-down {
    width: 0;
    height: 0;
    border-left: 30px solid transparent;
    border-right: 30px solid transparent;
    border-top: 50px solid rgb(49, 116, 240);
    margin: -15px auto auto auto;
    z-index: 3;
    position: fixed;
    left: 43%;
    right: 45%;
    bottom: 8%;
  }

